import React, { Component } from 'react';
import {withRouter} from 'react-router-dom';
import * as Parametres from '../../Param';
import axios from 'axios';
import './Panier.css';

class Panier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      history:this.props.history,
      Utilisateur:JSON.parse(sessionStorage.getItem("utilisateur")),
      id_Voyage:this.props.location.state.id_Voyage,
      prix:this.props.location.state.prix,
      id_panier:this.props.location.state.id_panier,
      panier:null,
      tabVoyages:null,
      prix_Golbal:0,
    }
  }
  componentWillMount(){
    console.log(this.state.Utilisateur);
    
    axios({
      url: Parametres.URL_TOMCAT+'/Bridge',
      method: 'post',
      data: {
        id_panier : this.state.id_panier,
        route: 'Panier/contenu',
      }
    }).then(res => {
      if (res.data !== ""){
        console.log(res);
        let data=[];
        for(let index=0; index<res.data.length;index++){
          this.state.tabVoyages.push(res.data[index].id_Voyage);
          this.setState({prix_Golbal:this.state.prix_Golbal+res.data[index].prix_voyage});
          data.push(<div id={res.data[index].id_article}><p>Nom du voyage : {res.data[index].nom_voyage}, prix : {res.data[index].prix_voyage}</p><hr/></div>);
        }
        this.setState({panier:data});
      }
    });
  }
  supprimer_voyage(id_article){
    axios({
      url: Parametres.URL_TOMCAT+'/Bridge',
      method: 'post',
      data: {
        id_article : id_article,
        route: 'Article/supprimerVoyage'
      }
    }).then(res => {
      document.getElementById(id_article).style.display="none";
    });
  }
  validerPanier(){
    axios({
      url: Parametres.URL_TOMCAT+'/Bridge',
      method: 'post',
      data: {
        id_panier : this.state.id_panier,
        id_utilisateur:this.state.Utilisateur.id,
        voyages:this.state.tabVoyages,
        prix:this.state.prix_Golbal,
        route: 'Panier/valider'
      }
    }).then(res => {
      this.state.history.push({
        pathname: Parametres.PREFIX_URL+'/acceuil',
      });
    });
  }
    render() {
    
      return (
        <div>
          <div>Panier</div>
          <hr/>
          <div>
            {this.state.panier}
          </div>
          <div>                 
            <button id="Valider_Panier" type="submit" value="Valider" onClick={this.validerPanier}>Valier panier</button>
          </div>
        </div>
      );
    }
  }
  
  export default withRouter(Panier);